import React from 'react';
import style from './footer.module.css'
import headPolice from "../../file/images/policeman.svg";
import vk from "../../file/images/vk-social-logotype.svg";
import youTube from "../../file/images/youtube-logotype.svg";
import telegram from "../../file/images/telegram.svg";
import big from "../../file/images/big-cogwheel.svg";
import {NavLink} from "react-router-dom";

function Footer() {
    return(
        <div className={style.main}>
            <div className={style.container}>
                <div className={style.footer}>
                    <div className={style.con_1}>
                        <NavLink to={'/'} className={style.p_l}>P
                            <img src={headPolice} alt=""/>
                        </NavLink>
                        <p>&#169; animePoliceman 2020</p>
                    </div>
                    <div className={style.network}>
                        <NavLink to={'/adminPanel'}>
                            <img className={style.shest} src={big} alt=""/>
                        </NavLink>
                        <a href="https://vk.com/animepoliceman">
                            <img className={style.vk} src={vk} alt=""/>
                        </a>
                        <a href="https://www.youtube.com/watch?v=dQw4w9WgXcQ">
                            <img className={style.you} src={youTube} alt=""/>
                        </a>
                        <a href="https://t.me/AnimePoliceman">
                            <img className={style.tele} src={telegram} alt=""/>
                        </a>
                    </div>
                </div>
            </div>
        </div>
    )


}




export default Footer