

const IS_LOGIN_IN = 'IS_LOGIN_IN';
const IS_LOGIN_OUT = 'IS_LOGIN_OUT';
const SET_USER_ID = 'SET_USER_ID';
const SET_USER_NAME = 'SET_USER_NAME';
const SET_USER_AVATAR = 'SET_USER_AVATAR';




let initialState = {
    authData: {
        /*name: 213, urlAvatar: 123, id: 2*/
    },
    isAuth: false
    /*followingInProgress: [1,2]*/
}



const authReducer = (state = initialState, action) =>  {


    switch (action.type) {
        case  IS_LOGIN_IN:
            return {
                ...state,
                isAuth: true
            }
        case IS_LOGIN_OUT:
            return {
                ...state,
                isAuth: false
            }
        case SET_USER_ID:
            let userId = {
                id: action.id
            }
            return {
                ...state,
                authData: {...state.authData , userId}
            }
        case SET_USER_NAME:
            let userName = action.name
            return {
                ...state,
                authData: {...state.authData , userName}
            }
        case SET_USER_AVATAR:
            let ava = action.ava
            return {
                ...state,
                authData: {...state.authData , ava}
            }

        default:
            return state;
    };
};



export let isLoginIn = () =>{
    return {
        type: IS_LOGIN_IN
    }
}
export let isLoginOut = () =>{
    return {
        type: IS_LOGIN_OUT
    }
}
export let setUserId = (id) =>{
    return {
        type: SET_USER_ID,
        id
    }
}
export let setUserName = (name) =>{
    return {
        type: SET_USER_NAME,
        name
    }
}
export let setUserAvatar = (ava) =>{
    return {
        type: SET_USER_AVATAR,
        ava
    }
}

export  default authReducer;