import React from 'react';
import {NavLink} from "react-router-dom";
import style from './../films.module.css'
import logogo from './../../../file/images/34e51a18e9188afefef49e4f5d8568c9.jpeg'


function FilmsElement(props) {

    return(

        <div className={style.main_el}>
            <div className={style.elem}>
                <NavLink to={'/filmId/' + props.id}>
                    <img className={style.elem_img} src={props.poster} alt=""/>
                    <div className={style.elem_name}>
                        <h3 className={style.elem_h3}>{props.name}</h3>
                    </div>
                    <span className={style.elem_ear}>{props.year}</span>
                </NavLink>
            </div>
        </div>

    )


}




export default FilmsElement