import React from 'react';
import style from './collectionId.module.css'
import FilmsElement from "../films/filmsElement/filmsElement";



function CollectionId(props) {


    const NewElem = props.filmData.map( e => {
        for(let i = 0; i < props.collectionData[props.id - 1].arr.length; i++){
            if(props.collectionData[props.id - 1].arr[i] == e.id){
                return <FilmsElement name={e.name} year={e.year} poster={e.poster} id={e.id} />
            }
        }
    })
    let collectionNew = props.collectionData[props.id - 1]

    return(
        <div>
            <div className={style.container}>
                <div className={style.main}>
                    <div className={style.main_dis}>
                        <p> <b>{collectionNew.name}</b> - {collectionNew.description}</p>
                    </div>
                    <div className={style.main_elem}>
                        {NewElem}
                    </div>
                </div>
            </div>
        </div>
    )


}




export default CollectionId