import React, {useState} from 'react';
import style from './filmId.module.css'
import {NavLink} from "react-router-dom";
import {Field, reduxForm} from "redux-form";
import photo from './../../file/images/b2e36d6bb659c159b14d13ef44717a45.png'



function CommentForm (props) {


    return (
        <form className={style.coment_form}  onSubmit={props.handleSubmit}>
            <div className={style.comment_con}><Field  component={"input"} name={"comment"}  className={style.comment_input} type="text"/></div>
            <button /*onClick={props.handleSubmit}*/ className={style.comment_Button}>Отправить</button>
        </form>
    );
}

const CommentReduxForm = reduxForm({
    form: 'Comment'
})(CommentForm)


function CommentElem (props) {
    return (
        <div className={style.messege_elem}>
            <div className={style.message_col_1}>
                <img src={props.data.url} alt=""/>
            </div>
            <div className={style.message_col_2}>
                <h3>{props.data.name}</h3>
                <p>{props.data.message}</p>
            </div>
            <div className={style.message_date}>{props.data.date}</div>
        </div>
    );
}




function FilmId(props) {

    const [error,setError] = useState(false)


    let film =  props.filmElem.filter( e => {
        return e.id == props.id;
    })



    let k = 0;
    let genresObj = []
    let comment = []

    if(props.filmElem.length != 0){
        genresObj = film[0].genres.map(e => {
            if (film[0].genres.length - 1 == k) {
                k++
                return <NavLink to={'/genres/' + e}>
                    {e}
                </NavLink>
            } else {
                k++
                return <NavLink to={'/genres/' + e}>
                    {e},&nbsp;
                </NavLink>
            }
        })

        if (film[0].comment){
            comment = film[0].comment.map(e => {
                return <CommentElem data={e} />
            })
        }

    }


    const addComment = (val) =>{
        let message = val.comment;
        let idC = 0;
        let name = props.authData.userName.name
        let url = 'https://firebasestorage.googleapis.com/v0/b/cinemapolice-4263f.appspot.com/o/UserAvatar%2Fv2WdiXNKZmfnJL4ms9zJReGxtsX2%2Fposter?alt=media&token=98123981-22c4-4638-89a7-4c8f5e9459a4'
        if(props.authData.ava){
            url = props.authData.ava
        }
        if(film[0].comment){
            idC = film[0].comment.length
        }
        if(props.isAuth){
            if((message != '') && (message != ' ') && (message != '  ') && (message != '   ')&& (message != '    ')){
                props.addCommentState(message,film[0].id,idC,name,url)
            }
            val.comment = '';
        }else {
            setError(true)
        }


    }


        return(
            <div >
                <div className={style.container}>
                    <div className={style.main}>
                        <div className={style.row_1}>
                            {props.filmElem.length != 0 && <img src={film[0].poster} alt=""/>}
                            <div className={style.row_1_name}>
                                <div className={style.row_1_name_1}>
                                    {props.filmElem.length != 0 && <h1>{film[0].name}</h1>}
                                    <p className={style.row_1_genres_befor}>Жанры: {genresObj}</p>
                                    {props.filmElem.length != 0 && <p>Режисёр: <NavLink to={'/stageDirector/' + film[0].stageDirector}>{film[0].stageDirector}</NavLink> </p>}
                                    {props.filmElem.length != 0 && <p>Год: <NavLink to={'/year/' + film[0].year }>{film[0].year}</NavLink> </p>}
                                </div>
                                <div className={style.row_1_2_apl}>
                                    {props.filmElem.length != 0 && <span>{film[0].appraisal} / 10</span>}
                                </div>
                            </div>
                        </div>
                        <div className={style.row_2_dis}>
                            <h3>Описание:</h3>
                            {props.filmElem.length != 0 && <p>{film[0].description}</p>}
                        </div>
                        <div className={style.film}>
                            <h2>Смотреть:</h2>
                            <div className={style.proigrvatel}>
                                {/*<iframe src="https://zloekino.su/video/976101-Proekt.Power-Film" width="100%" height="100%" frameborder="0"></iframe>*/}
                                {props.filmElem.length != 0 && <iframe
                                    src={film[0].src}
                                    width="100%" height="100%" frameBorder="0" allowFullScreen></iframe>}
                            </div>
                        </div>
                        <div className={style.comment}>
                            <h3>Коментарии</h3>
                            {error && <div className={style.error}>
                                        <p>А войти не хочешь???</p>
                                        <span onClick={() => {setError(false)}}>x</span>
                                      </div>}
                            <div className={style.messege_main}>
                                {props.filmElem.length != 0 && comment}
                            </div>
                            <CommentReduxForm onSubmit={addComment} />
                        </div>
                    </div>
                </div>
            </div>
        )

}




export default FilmId