import React, {useState} from 'react';
import {NavLink} from "react-router-dom";
import style from './films.module.css'
import search from './../../file/images/magnifying-glass.svg';
import close from  './../../file/images/close-cross-in-circular-outlined-interface-button.svg';
import FilmsElement from "./filmsElement/filmsElement";
import {Field, reduxForm} from "redux-form";

function formFilms(props) {

    return(
        <form className={style.Search_form} onSubmit={props.handleSubmit}>
            <div className={style.searh_pop}>
                <Field component={"input"} name={"search"} className={style.Search_input}></Field>
                <button className={style.Search_submit}>
                    {props.check ?
                        <img  src={search} alt=""/>
                    :
                        <img  src={close} alt=""/>
                    }
                </button>
            </div>
        </form>
    )
}

const FilmReduxForm = reduxForm({
    form: 'loginIn'
})(formFilms)


function Films(props) {

    const [elem,setElem] = useState(true)
    const [text,setText] = useState('')

    const FilmObject1 = props.filmData.map( e => {
        return <FilmsElement name={e.name} year={e.year} poster={e.poster} id={e.id}/>
    })
    const FilmFilter = props.filmData.filter( e => {
        if(e.name.includes(text)){
            return e
        }
    })


    let FilmObject2 = [];
    if (FilmFilter.length == 0){
        FilmObject2 = <p className={style.notFound}>ничего не найдено</p>
    }else {
        FilmObject2 = FilmFilter.map( e => {
            return <FilmsElement name={e.name} year={e.year} poster={e.poster} id={e.id}/>
        })
    }



    const GenreObject = props.genres.map(e => {
        return <NavLink to={'/genres/' + e} >{e},  </NavLink>
    })

    const searchGo = (e) => {
        if(elem){
            setElem(false)
            setText(e.search)

        }else {
            setElem(true)
            e.search = ''
        }

    }

    return(
        <div >
            <div className={style.Search}>
                <div className={style.Search_in}>
                    <FilmReduxForm onSubmit={searchGo} check={elem}/>
                </div>
            </div>
            <div className={style.container}>
                <div className={style.main}>
                    <div className={style.main_el}>
                        {elem ? FilmObject1 : FilmObject2}
                    </div>
                    <div className={style.genres}>
                        <div className={style.genres_con}>
                            <h2>Жанры</h2>
                            <div className={style.genres_con_a}>
                                {GenreObject}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )


}




export default Films