
import style from './../mainPage.module.css';
import React from "react";
import Slider from "react-slick";



class SimpleSlider extends React.Component {

    constructor(props) {
        super(props);
        this.state = { counter: [] };
    }


    componentDidMount() {
         /*this.state.counter = this.props.filmData.map(e => {
                 return <SliderElem filmData={e.filmData} />
         })*/

    }

    render() {
        const settings1 = {
            dots: true,
            infinite: true,
            speed: 1000,
            slidesToShow: 5,
            slidesToScroll: 2,
            arrows: false,
            autoplay: true,
            autoplaySpeed: 3000,
            pauseOnHover: true,
            cssEase: "ease",
            draggable:false
        };
        const settings2 = {
            dots: true,
            infinite: true,
            speed: 1000,
            slidesToShow: 3,
            slidesToScroll: 1,
            arrows: false,
            autoplay: true,
            autoplaySpeed: 1500,
            pauseOnHover: true,
            cssEase: "ease",
            draggable:false
        };
        const settings3 = {
            dots: true,
            infinite: true,
            speed: 1000,
            slidesToShow: 1,
            slidesToScroll: 1,
            arrows: false,
            autoplay: true,
            autoplaySpeed: 2000,
            pauseOnHover: true,
            cssEase: "ease",
            draggable:false
        };

        return (
            <div>
                <Slider {...settings1} className={style.carousel_5}>
                    {this.props.filmData}
                    {/*<a href="##" className={style.carousel_item}>
                        <img src={car_2} alt=""/>
                    </a>
                    <a href="##" className={style.carousel_item}>
                        <img src={car_3} alt=""/>
                    </a>
                    <a href="##" className={style.carousel_item}>
                        <img src={car_4} alt=""/>
                    </a>
                    <a href="##" className={style.carousel_item}>
                        <img src={car_5} alt=""/>
                    </a>*/}

                </Slider>
                <Slider {...settings2} className={style.carousel_3}>
                    {this.props.filmData}
                </Slider>
                <Slider {...settings3} className={style.carousel_1}>
                    {this.props.filmData}
                </Slider>
            </div>
        );
    }
}


export default SimpleSlider;