import React from 'react';
import style from './mainPage.module.css'
import SimpleSlider from "./calousel/carouselMain"
import SliderElem from "./calousel/carouselElem";
import {NavLink} from "react-router-dom";

import FilmsElement from "../films/filmsElement/filmsElement";



function MainPage(props) {

    let k = 0;
    let obj = [...props.filmData].reverse()
        .filter( e => {
            let t = Math.random()*100
            if((t > 75) && (k < 15)){
                k++
                return e
            }
    })

    let n = 0;
    let best = props.filmData.map( e => {
        if((e.best == true) && (n < 5)){
            n++
            return <FilmsElement name={e.name} year={e.year} poster={e.poster} id={e.id}/>
        }
    })

    let oobj2 = obj.map( e => {
        return <SliderElem filmData={e} />
    })



    let filmsNF = props.filmData.filter(e => {
        if(e.genres.includes("научная фантастика", 0)){
            return e
        }
    })


    let  q = 0;
    let filmsElNF = filmsNF.map(e => {
        if(q < 5){
            q++
            return <FilmsElement name={e.name} year={e.year} poster={e.poster} id={e.id}/>
        }
    })

    let podborka = props.collectionData.map(e => {
        return  <div className={style.podborka_el}>
                    <NavLink to={'/collectionId/' + e.id} >
                        <img src={e.poster} alt=""/>
                    </NavLink>
                </div>
    })



    return(
        <div>
            <div className={style.page_1}>
                <SimpleSlider filmData={oobj2} />
            </div>
            <div className={style.page_2}>
                <p>Подборки</p>
                <div className={style.container}>
                    <div className={style.podborka}>
                        {podborka}
                        {/*<div className={style.podborka_el}>
                            <NavLink to={'/collectionId/1'} >
                                <img src={podborka_1} alt=""/>
                            </NavLink>
                        </div>
                        <div className={style.podborka_el}>
                            <NavLink to={'/collectionId/2'} >
                                <img src={podborka_2} alt=""/>
                            </NavLink>
                        </div>
                        <div className={style.podborka_el}>
                            <NavLink to={'/collectionId/3'} >
                                <img src={podborka_3} alt=""/>
                            </NavLink>
                        </div>*/}
                    </div>
                </div>
            </div>
            <div className={style.page_3}>
                <p>Лучшее</p>
                <div className={style.container}>
                    <div className={style.best}>
                        {best}
                    </div>
                </div>
            </div>
            <div className={style.page_4}>
                <p>Научная фантастика</p>
                <div className={style.container}>
                    <div className={style.NFFilms}>
                        {filmsElNF}
                    </div>
                </div>
            </div>
        </div>
    )


}




export default MainPage