import React, {useState} from 'react';
import style from './login.module.css';
import {Field, reduxForm} from "redux-form";
import * as firebase from "firebase";
import {Redirect} from "react-router-dom";










function LoginInForm (props) {

    return (
        <form className={style.LoginIn} onSubmit={props.handleSubmit}>
            <span>Почта:</span>
            <Field  component={"input"} name={"login"}  className={style.LoginIn_input_login} type="text"/>
            <span>Пароль:</span>
            <Field  component={"input"} name={"password"}  className={style.LoginIn_input_password} type="password"/>
            <button /*onClick={props.handleSubmit}*/ className={style.LoginIn_Button}>Отправить</button>
        </form>
    );
}

const LoginInReduxForm = reduxForm({
    form: 'loginIn'
})(LoginInForm)



function RegistrForm (props) {


    return (
        <form className={style.LoginIn}  onSubmit={props.handleSubmit}>
            <span>Введите Почту:</span>
            <Field  component={"input"} name={"login"}  className={style.LoginIn_input_login} type="text"/>
            <span>Придумайте ник:</span>
            <Field  component={"input"} name={"name"}  className={style.LoginIn_input_name} type="text"/>
            <span>Придумайте пароль:</span>
            <Field  component={"input"} name={"password"}  className={style.LoginIn_input_password} type="password"/>
            <button /*onClick={props.handleSubmit}*/ className={style.LoginIn_Button}>Отправить</button>
        </form>
    );
}

const RegistrReduxForm = reduxForm({
    form: 'Registr'
})(RegistrForm)


function Login (props) {

    const [error, setError] = useState(false);
    const erorClose = () => {
        setError(false)
    }


    let [editMode,setEditMode] =  useState(false);

    const otrisovka = () => {
        setEditMode(!editMode)
    }


    const addLoginInValid = (value) =>{
        let login = value.login;
        let password = value.password;
        if((login != null) && (password != null)){
            addLoginIn(value)
        }else {
            setError(true)
        }
    }



    const addLoginIn = (value) => {

        let login = value.login;
        let password = value.password;
        firebase.auth().signInWithEmailAndPassword(login,password)
            .then(
                error => {
                    console.log(error);
                    props.isLoginIn();
                }
            )
    }
    const addRegistrValid = (value) =>{
        let login = value.login;
        let password = value.password;
        let name = value.name;
        if((login != null) && (password != null) && (name != null)){
            addRegistr(value)
        }else {
            setError(true)
        }
    }
    const addRegistr = (value) => {
        let login = value.login;
        let password = value.password;
        let name = value.name;
        firebase.auth().createUserWithEmailAndPassword(login,password)
            .then(
                error => {
                    console.log(error)
                    props.isLoginIn();
                }
            )
        let uuuser = [];
        firebase.auth().onAuthStateChanged((user) => {
            uuuser = user
        });
        const timeFun = () => {
            let userId = uuuser.uid
            /*db.ref('User/'+userId ).push(naname)*/
            firebase.database().ref('User/'+userId ).set({
                name: name
            })
        }
        setTimeout(
            timeFun
        , 2000)
    }

    if(props.isAuth){
        return <Redirect to={"/personalArea"} />
    }else {
        return (
            <div className={style.Login}>
                <div className={style.login_con}>

                    <h1> {!editMode ? "Залогинься" : "Регистрация"}</h1>
                    {(error) &&
                    <div className={style.error}>атата <div className={style.error_x} onClick={erorClose}>x</div></div>}
                    {!editMode ?
                        <LoginInReduxForm onSubmit={addLoginInValid}/>
                        :
                        <RegistrReduxForm onSubmit={addRegistrValid}/>}
                    {/* {!editMode ? <span onClick={otrisovka}>LoginIn</span> : <span onClick={otrisovka}>Register</span>}*/}
                    <span onClick={otrisovka}>
                    {!editMode ? "Регистрация" : "Вход"}
                </span>
                </div>
            </div>
        );
    }
}

export default Login;