export let attemptSet = (el) =>{
    return {
        type: ATTEMPT,
        el
    }
}
export let sendPoster = (id,poster) =>{
    return {
        type: SEND_POSTER,
        id,poster
    }
}

export let setCount = (count) =>{
    return {
        type: SET_COUNT,
        count
    }
}

export let addNewFilm = (year,name,description,appraisal,stageDirector,best,genres,src,id,poster) => {
    return async (dispatch) => {
        const db = firebase.database();
        if (best) {
            best = best
        } else {
            best = false
        }

        let meme = {
            name: name,
            description: description,
            year: year,
            stageDirector: stageDirector,
            genres: genres,
            appraisal: appraisal,
            id: id,
            src: src,
            delete: false,
            best: best
        }

        await db.ref('DataFilm/' + id).set(meme)


        const uploadTask = firebase.storage().ref('images/'+ id +  '/poster' ).put(poster)


        uploadTask.on('state_changed' ,
            (snapshot) => {
                let progress = Math.ceil((snapshot.bytesTransferred / snapshot.totalBytes)*100)
                // dispatch(progressPoster1(progress))
                console.log(progress)
            },
            (error) => {console.log(error)},
            () => {
                console.log('redy')
            })


    }
}

export let ChangeFilmState = (year,name,description,appraisal,stageDirector,best,genres,src,id) =>{
    return async (dispatch) => {
        const db2 = firebase.database();
        if (best) {
            best = best
        } else {
            best = false
        }

        let meme = {
            name: name,
            description: description,
            year: year,
            stageDirector: stageDirector,
            genres: genres,
            appraisal: appraisal,
            id: id,
            src: src,
            delete: false,
            best: best
        }


        await db2.ref('DataFilm/' + id).set(meme)
    }
}

export let addCommentState = (message,id,idC,name,url) =>{
    return async (dispatch) => {
        const db2 = firebase.database();

        let now = new Date;
        let day = now.getDate()
        if(day < 10){day = '0' + day}
        let month = now.getMonth() + 1
        if(month < 10){month = '0' + month}
        let date = day + '.' + month

        let meme = {
            message: message,
            url: url,
            name: name,
            date: date
        }

        await db2.ref('DataFilm/' + id + '/comment/' + idC).set(meme)
    }
}

