import React from 'react';
import './App.css';
import {BrowserRouter, Route} from "react-router-dom";
import {connect} from "react-redux";
import {compose} from "redux";
import MainPageContainer from "./component/mainPage/mainPageContainer";
import Footer from "./component/Footer/footer";
import HeaderContainer from "./component/header/headerContainer";

import FilmsContainer from "./component/films/FilmsContainer";
import CollectionsContainer from "./component/collections/collectionsContainer";
import NewsContainer from "./component/news/newsContainer";
import FilmIdContainer from "./component/filmId/FilmIdContainer";
import LoginContainer from "./component/Login/LoginCon";
import CollectionIdContainer from "./component/collectionId/collectionIdContainer";
import WithRouterGenres from "./component/ganres/ganresContainer";
import AdminPanelContainer from "./component/adminPanel/adminPanelContainer";
import * as firebase from "firebase";
import {attemptSet, sendPoster, setCount} from "./redux/filmReduser";
import WithRouterStageDirector from "./component/stageDirector/stageDirectorContainer";
import WithRouterYear from "./component/year/yearContainer";
import {isLoginIn, setUserAvatar, setUserId, setUserName} from "./redux/authReduser";
import PersonalAreaContainer from "./component/personalArea/personalAreaContainer";

class App extends React.Component {

  constructor(props) {
    super(props);
    this.state = {check: false};
  }


  componentDidMount() {

      const db = firebase.database();
      let game = db.ref('DataFilm')
      game.on('value', (elem) => {
        this.kek(elem.val())
      })

      firebase.auth().onAuthStateChanged((user) => {
        if(user){
          this.props.isLoginIn()
          this.props.setUserId(user.uid)
          let userName = db.ref('User/' + user.uid)
          userName.on('value', (e) => {
                this.props.setUserName(e.val())
            /*console.log(e)
            this.props.setUserName(e)*/
          })
          firebase.storage().ref('UserAvatar').child(user.uid + '/poster').getDownloadURL().then( url => {
            this.props.setUserAvatar(url);
          })

          console.log(user)
        }
      })

    }

    kek(e) {
      this.props.attemptSet(e)

      let id = e.length;
      this.props.setCount(id)
      let id3 = [];
      /*this.props.clearGameState();
      let id = 0;
      let id2 = 0;
      let id3 = [];


      for (let key in e) {
        this.props.getGameData(e[key].nameGame , e[key].description, e[key].price,e[key].novelty,e[key].popular,e[key].appraisal,e[key].delete)
        id++
      }*/

      for(let i = 0; i < id; i++){
        id3[i] = i
      }


      const posterF = (k) => {
        firebase.storage().ref('images').child(k + '/poster').getDownloadURL().then( url => {
          this.props.sendPoster(k,url);
        })
      }

      async function posledArray(id3) {
        for (const e of id3){
          await posterF(e)
        }
      }
      posledArray(id3)
  }


  componentDidUpdate(prevProps, prevState, snapshot) {

  }

  render() {
    return (
        <BrowserRouter basename={process.env.PUBLIC_URL}>
          <div className="App">
            <div className="app_main">
              <HeaderContainer/>
              <div className="app-wrapper-content">
                <Route exact path="/" render={() => <MainPageContainer/>}/>
                <Route exact path="/films" render={() => <FilmsContainer/>}/>
                <Route exact path="/collections" render={() => <CollectionsContainer/>}/>
                <Route exact path="/news" render={() => <NewsContainer/>}/>
                <Route exact path="/filmId/:id?" render={() => <FilmIdContainer/>}/>
                <Route exact path="/login" render={() => <LoginContainer/>}/>
                <Route exact path="/collectionId/:id?" render={() => <CollectionIdContainer/>}/>
                <Route exact path="/genres/:genre?" render={() => <WithRouterGenres/>}/>
                <Route exact path="/adminPanel" render={() => <AdminPanelContainer/>}/>
                <Route exact path="/stageDirector/:stageDirector?" render={() => <WithRouterStageDirector/>}/>
                <Route exact path="/year/:year?" render={() => <WithRouterYear/>}/>
                <Route exact path="/personalArea" render={() => <PersonalAreaContainer/>}/>
              </div>
              <Footer/>
            </div>
          </div>
        </BrowserRouter>
    );
  }
}



let mapStateToProps = (state) => {
  return {

  }
};
export default compose(connect(mapStateToProps , {attemptSet,sendPoster,setCount,isLoginIn,setUserName,setUserId,setUserAvatar}))(App)