import React from 'react';
import style from './collections.module.css'
import CollectionsElements from "./collectionElement/collectionElement";


function Collections(props) {

    const NewElem = props.collectionData.map( e => {
        return <CollectionsElements id={e.id} name={e.name} description={e.description} poster={e.poster} />
    })

    return(
        <div>
            <div className={style.container}>
                <div className={style.main}>
                    <div className={style.zagolovok}>
                        <h1>Подборки</h1>
                    </div>
                    {NewElem}
                </div>
            </div>
        </div>
    )


}




export default Collections