import React from 'react';
import style from './news.module.css'
import photo from './../../file/images/ears.jpg'


function News() {

    return(
        <div>
            <div className={style.container}>
                <div className={style.main}>
                    <div className={style.elem}>
                        <img src={photo} alt=""/>
                        <div className={style.podpipi}>
                            <h3>Хз чё тут писать</h3>
                            <p>если есть предложения то в ЛС, а так типо вот новость: Навальный уже в Германии</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )


}




export default News