import React, {useState} from 'react';
import style from './personalArea.module.css'
import photo from './../../file/images/ears.jpg'
import {Redirect} from "react-router-dom";
import * as firebase from "firebase";

function PersonalArea(props) {

    const isLoginOutDataBase = () => {
        firebase.auth().signOut().then(function() {
            props.isLoginOut()
        })
    }

    const [imageAsFile, setImageAsFile] = useState('')



    const onChangeFile = (e) =>{
        const image = e.target.files[0];
        setImageAsFile((imageFile ) => image)
    }

    const setAvatar = () => {
        if(imageAsFile){
            const uploadTask = firebase.storage().ref('UserAvatar/'+ props.authData.userId.id +  '/poster' ).put(imageAsFile)


            uploadTask.on('state_changed' ,
                (snapshot) => {
                    let progress = Math.ceil((snapshot.bytesTransferred / snapshot.totalBytes)*100)
                    // dispatch(progressPoster1(progress))
                    console.log(progress)
                },
                (error) => {console.log(error)},
                () => {
                    console.log('redyAvatar')
                })
        }
    }


    if(props.isAuth) {
        return (
            <div>
                <div className={style.container}>
                    <div className={style.main}>
                        <div className={style.elem}>
                            <div className={style.img_container}>
                                {(props.authData.ava == undefined) ? <div className={style.img_block}>
                                        <p>у вас нет аватарки, загрузите её</p>
                                        <input onChange={onChangeFile} type="file"/>
                                        <button onClick={setAvatar}>отправить</button>
                                    </div> :
                                    <div>
                                        <img src={props.authData.ava} alt=""/>
                                        <p>Изменить аватарку</p>
                                        <input onChange={onChangeFile} type="file"/>
                                        <button onClick={setAvatar}>отправить</button>
                                    </div>
                                }
                            </div>
                            <div className={style.name}>
                                {props.authData.userName ? props.authData.userName.name : <p>name</p>}
                            </div>
                            <div onClick={isLoginOutDataBase} className={style.logOff}>
                                Выйти
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }else {
        return <Redirect to={"/login"} />
    }


}




export default PersonalArea