import React from 'react';
import {compose} from "redux";
import {connect} from "react-redux";
import Ganres from "./ganres";
import {withRouter} from "react-router-dom";






class GanresContainer extends React.Component{
   componentDidMount() {

   }

    render(){
        return (
            <Ganres filmData={this.props.filmData} genre={this.props.match.params.genre} />
        )
    }



}

let mapStateToProps = (state) => {
    return {
        filmData: state.Film.filmData
    }
}

let WithRouterGenres = withRouter(GanresContainer)

export default compose(
    connect(mapStateToProps , {

    })
)(WithRouterGenres)

