import React, {createRef, useState} from 'react';
import style from './header.module.css'
import headPolice from './../../file/images/policeman.svg'
import {NavLink} from "react-router-dom";


function Header(props) {
    const [menu,setMenu] = useState(false)
    const  ref_hidden = React.createRef()

    const setMenuHidden = () => {
        if(!menu){
            ref_hidden.current.style.display = "flex"
            ref_hidden.current.style.right = "0px";
            document.body.style.overflowY = "hidden"
            setMenu(true)
        }else {
            ref_hidden.current.style.right = "-300px";
            document.body.style.overflowY = "scroll"
            setTimeout({
                hihiden
            },2000)
            function hihiden(){ref_hidden.current.style.display = "none"}
            setMenu(false)
        }
    }
    const leaveMenu = () => {
        if(!menu){
            /*ref_hidden.current.style.right = "0px";
            setMenu(true)*/
        }else {
            ref_hidden.current.style.right = "-300px";
            document.body.style.overflowY = "scroll"
            setTimeout({
                hihiden
            },2000)
            function hihiden(){ref_hidden.current.style.display = "none"}
            setMenu(false)
        }
    }




    return(
        <div className={style.header} id="outer-container">
            <NavLink to={'/'} className={style.logo_con}>
                <span>cinema</span>
                <span className={style.p_l}>P
                    <img src={headPolice} alt=""/>
                </span>
                <span>olice</span>

            </NavLink>
            <div className={style.main}>
                <div className={style.el}>
                    <NavLink to={'/films'} href="##" activeClassName={style.activeLink}>Фильмы</NavLink>
                </div>
                <div className={style.el}>
                    <NavLink to={'/collections'} href="##" activeClassName={style.activeLink}>Подборки</NavLink>
                </div>
                <div className={style.el}>
                    <NavLink to={'/news'} href="##" activeClassName={style.activeLink}>Новости</NavLink>
                </div>


                <div className={style.el}>
                    {props.isAuth ? <NavLink to={'/personalArea'}  href="##" activeClassName={style.activeLink}>Личный кабинет</NavLink>
                       :   <NavLink to={'/login'}  href="##" activeClassName={style.activeLink}>Войти</NavLink>}
                </div>
            </div>
            <div onClick={setMenuHidden} className={style.menu}>
                <span className={style.palka}></span>
                <span className={style.palka}></span>
                <span className={style.palka}></span>
            </div>
            <div ref={ref_hidden} className={style.hidden_menu} onMouseLeave={leaveMenu}>
                <div className={style.close_menu} onClick={setMenuHidden}>
                    <h1>cinemaPolice</h1>
                    <span><b>x</b></span>
                </div>
                <NavLink to={'/films'} className={style.hidden_menu_el} href="##">Фильмы</NavLink>
                <NavLink to={'/collections'} className={style.hidden_menu_el} href="##">Подборки</NavLink>
                <NavLink to={'/news'} className={style.hidden_menu_el} href="##">Новости</NavLink>
                {props.isAuth ?
                    <NavLink to={'/personalArea'} className={style.hidden_menu_el} href="##">Личный кабинет</NavLink>
                    :
                    <NavLink to={'/login'} className={style.hidden_menu_el} href="##">Войти</NavLink>
                }

            </div>
        </div>
    )


}




export default Header