import React from 'react';
import {compose} from "redux";
import {connect} from "react-redux";
import PersonalArea from "./personalArea";
import {isLoginOut} from "../../redux/authReduser";






class PersonalAreaContainer extends React.Component{
   componentDidMount() {

   }

    render(){
        return (
            <PersonalArea isLoginOut={this.props.isLoginOut} authData={this.props.authData} isAuth={this.props.isAuth} />
        )
    }



}

let mapStateToProps = (state) => {
    return {
        authData: state.isAuthM.authData,
        isAuth: state.isAuthM.isAuth
    }
}


export default compose(
    connect(mapStateToProps , {
        isLoginOut
    })
)(PersonalAreaContainer)

