

import podborka_1 from "./../file/images/podborka_4.jpg";
import podborka_2 from "./../file/images/podborka_5.jpg";
import podborka_3 from "./../file/images/podborka_6.jpg";

const IS_LOGIN_IN = 'IS_LOGIN_IN';
const IS_LOGIN_OUT = 'IS_LOGIN_OUT';



let initialState = {
    collectionData: [
        {id: 1, name: 'Старые фильмы', description: "старые фильмы(я считаю фильмы старыми если им больше 25 лет), да я так считаю и что?", poster: podborka_1, arr: [1,33,37,38,86,84,93,97,99,105,106,113]},
        {id: 2, name: 'Поплакать', description: "ну тут и пояснять нечего, фильмы цепляющие за душу ", poster: podborka_2, arr: [8,35,45,102,104,106,107]},
        {id: 3, name: 'Осенние настроение', description: "фильмы под осенние настроение или нет, мне всёровно впринципе", poster: podborka_3, arr: [2,7,28,64,67,101]}
    ]
}



const collectionReducer = (state = initialState, action) =>  {


    switch (action.type) {
        case  IS_LOGIN_IN:
            return {
                ...state,
                isAuth: true
            }
        case IS_LOGIN_OUT:
            return {
                ...state,
                isAuth: false
            }

        default:
            return state;
    };
};



export let isLoginIn = () =>{
    return {
        type: IS_LOGIN_IN
    }
}
export let isLoginOut = () =>{
    return {
        type: IS_LOGIN_OUT
    }
}




export  default collectionReducer;