import React from 'react';
import {compose} from "redux";
import {connect} from "react-redux";
import FilmId from "./filmId";
import {withRouter} from "react-router-dom";
import {addCommentState} from "../../redux/filmReduser";






class FilmIdContainer extends React.Component {
    componentDidMount() {

    }


    render() {
        return (
            <FilmId id={this.props.match.params.id} filmElem={this.props.filmData} authData={this.props.authData} addCommentState={this.props.addCommentState} isAuth={this.props.isAuth}/>)
    }
}
let mapStateToProps = (state) => {
    return {
        filmData: state.Film.filmData,
        authData: state.isAuthM.authData,
        isAuth: state.isAuthM.isAuth
    }
}
let WithRouterFilm = withRouter(FilmIdContainer)

export default compose(
    connect(mapStateToProps , {
        addCommentState
    })
)(WithRouterFilm)

