import React from 'react';
import {compose} from "redux";
import {connect} from "react-redux";
import CollectionId from "./collectionId";
import {withRouter} from "react-router-dom";






class CollectionIdContainer extends React.Component{
   componentDidMount() {

   }

    render(){
        return (
            <CollectionId id={this.props.match.params.id} collectionData={this.props.collectionData} filmData={this.props.filmData} />
        )
    }



}

let mapStateToProps = (state) => {
    return {
        collectionData: state.Collection.collectionData,
        filmData: state.Film.filmData
    }
}

let WithRouterCollection = withRouter(CollectionIdContainer)

export default compose(
    connect(mapStateToProps , {

    })
)(WithRouterCollection)

