import React from 'react';
import {compose} from "redux";
import {connect} from "react-redux";
import StageDirector from "./stageDirector";
import {withRouter} from "react-router-dom";






class StageDirectorContainer extends React.Component{
   componentDidMount() {

   }

    render(){
        return (
            <StageDirector filmData={this.props.filmData} stageDirector={this.props.match.params.stageDirector} />
        )
    }



}

let mapStateToProps = (state) => {
    return {
        filmData: state.Film.filmData
    }
}

let WithRouterStageDirector = withRouter(StageDirectorContainer)

export default compose(
    connect(mapStateToProps , {

    })
)(WithRouterStageDirector)

