import React from 'react';
import style from './stageDirector.module.css'
import FilmsElement from "../films/filmsElement/filmsElement";





function StageDirector(props) {

    let films = props.filmData.filter(e => {
        if(e.stageDirector.includes(props.stageDirector, 0)){
            return e
        }
    })

    let filmsEl = films.map(e => {
        return <FilmsElement name={e.name} year={e.year} poster={e.poster} id={e.id}/>
    })

    return(
        <div >
            <div className={style.container}>
                <p className={style.logo}>{props.stageDirector}</p>
                <div className={style.main}>
                    {filmsEl}
                </div>
            </div>
        </div>
    )


}




export default StageDirector