import React from 'react';
import style from './../collections.module.css'
import {NavLink} from "react-router-dom";



function CollectionsElements(props) {

    return(
        <div>
            <NavLink to={'/collectionId/'+ props.id} className={style.element}>
                <img className={style.images} src={props.poster} alt=""/>
                <div className={style.elem_con}>
                    <h3>{props.name}</h3>
                    <p>{props.description} </p>
                </div>
            </NavLink>
        </div>
    )


}




export default CollectionsElements